import React, { useState, useEffect, useCallback } from 'react';
import {
  Container,
  Paper,
  Typography,
  TextField,
  Button,
  Box,
  Alert,
  CircularProgress,
  Card,
  CardContent
} from '@mui/material';
import { useSnackbar } from 'notistack';
import api from '../api/axios';
import { useAuth } from '../contexts/AuthContext';

interface SupportTicket {
  id: string;
  title: string;
  description: string;
  status: string;
  createdAt: string;
}

const SupportPage: React.FC = () => {
  const { enqueueSnackbar } = useSnackbar();
  const { user } = useAuth();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [formData, setFormData] = useState({
    title: '',
    description: ''
  });
  const [tickets, setTickets] = useState<SupportTicket[]>([]);

  const fetchTickets = useCallback(async () => {
    try {
      const response = await api.get('/support/tickets');
      setTickets(response.data);
    } catch (err: any) {
      console.error('Error fetching support tickets:', err);
      enqueueSnackbar('Ошибка при получении тикетов', { variant: 'error' });
    }
  }, [enqueueSnackbar]);

  useEffect(() => {
    if (user) {
      fetchTickets();
    }
  }, [user, fetchTickets]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!formData.title.trim() || !formData.description.trim()) {
      setError('Все поля обязательны для заполнения');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      await api.post('/support', {
        title: formData.title,
        description: formData.description,
        userId: user?.id
      });

      enqueueSnackbar('Тикет успешно создан', { variant: 'success' });
      setFormData({ title: '', description: '' });
      await fetchTickets();
    } catch (err: any) {
      console.error('Error creating support ticket:', err);
      setError(err.response?.data?.message || 'Ошибка при создании тикета');
      enqueueSnackbar('Ошибка при создании тикета', { variant: 'error' });
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  return (
    <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          Служба поддержки
        </Typography>
        <Typography variant="body1" paragraph>
          Если у вас возникли вопросы или проблемы, пожалуйста, заполните форму ниже.
          Наша команда поддержки свяжется с вами в ближайшее время.
        </Typography>

        {error && (
          <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
            {error}
          </Alert>
        )}

        <Box component="form" onSubmit={handleSubmit} noValidate>
          <TextField
            name="title"
            label="Тема"
            value={formData.title}
            onChange={handleInputChange}
            fullWidth
            required
            margin="normal"
            error={!!error && !formData.title.trim()}
          />
          <TextField
            name="description"
            label="Описание проблемы"
            value={formData.description}
            onChange={handleInputChange}
            fullWidth
            required
            multiline
            rows={4}
            margin="normal"
            error={!!error && !formData.description.trim()}
          />
          <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              disabled={loading}
              sx={{ minWidth: 120 }}
            >
              {loading ? <CircularProgress size={24} /> : 'Отправить'}
            </Button>
          </Box>
        </Box>

        {tickets.length > 0 && (
          <>
            <Typography variant="h6" component="h2" sx={{ mt: 4, mb: 2 }}>
              Ваши тикеты
            </Typography>
            {tickets.map((ticket) => (
              <Card key={ticket.id} sx={{ mb: 2 }}>
                <CardContent>
                  <Typography variant="h6" component="div">
                    {ticket.title}
                  </Typography>
                  <Typography color="text.secondary" sx={{ mb: 2 }}>
                    {ticket.description}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    Статус: {ticket.status}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    Создан: {new Date(ticket.createdAt).toLocaleString()}
                  </Typography>
                </CardContent>
              </Card>
            ))}
          </>
        )}
      </Paper>
    </Container>
  );
};

export default SupportPage;